import { createAction } from 'redux-actions';
import axios from 'axios';
import { navigate } from '@reach/router';
import _ from 'lodash';
import { writeStateToCookie } from '../utils/index';
import { dataTransferService } from '../servicesAndCharacteristics';

axios.defaults.baseURL = 'https://portal.pipe.today/api';

///////////////////////
/////// other ////////
/////////////////////

export const showAppPreloader = createAction('SHOW_APP_PRELOADER');

///////////////////////
//// auth actions ////
/////////////////////

export const fetchTokenRequest = createAction('FETCH_TOKEN_REQUEST');
export const fetchTokenSuccess = createAction('FETCH_TOKEN_SUCCESS');
export const fetchTokenFailure = createAction('FETCH_TOKEN_FAILURE');

export const convertToken = (data, type, appId) => {
    return (dispatch) => {
        dispatch(fetchTokenRequest());

        axios
            .post('oauth/convert-token/', {
                grant_type: 'convert_token',
                client_id: appId,
                backend: type,
                token: data,
            })
            .then((response) => {
                writeStateToCookie(
                    response.data,
                    'auth_cookie',
                    response.data.expires_in
                );
                dispatch(fetchTokenSuccess(response.data));
            })
            .catch(() => {
                dispatch(fetchTokenFailure());
            });
    };
};

///////////////////////
////// recipes ///////
/////////////////////

export const fetchRecipesRequest = createAction('FETCH_RECIPES_REQUEST');
export const fetchRecipesSuccess = createAction('FETCH_RECIPES_SUCCESS');
export const fetchRecipesFailure = createAction('FETCH_RECIPES_FAILURE');

export const recipe = () => {
    return (dispatch) => {
        dispatch(fetchRecipesRequest());
        dispatch(showAppPreloader(true));

        axios
            .get('recipe/')
            .then((response) => {
                dispatch(fetchRecipesSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch(() => {
                dispatch(fetchRecipesFailure());
                dispatch(showAppPreloader(false));
            });
    };
};

///////////////////////
//////// user ////////
/////////////////////

export const fetchUserRequest = createAction('FETCH_USER_REQUEST');
export const fetchUserSuccess = createAction('FETCH_USER_SUCCESS');
export const fetchUserFailure = createAction('FETCH_USER_FAILURE');

export const user = () => {
    return (dispatch, getState) => {
        dispatch(fetchRecipesRequest());
        dispatch(showAppPreloader(true));
        const {
            auth: {
                cookie: { access_token },
            },
        } = getState();
        axios
            .get('user/', {
                headers: {
                    Authorization: `Bearer ${access_token}`,
                },
            })
            .then((response) => {
                dispatch(fetchUserSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch((e) => {
                dispatch(fetchUserFailure());
                dispatch(showAppPreloader(false));

                switch (e.response.status) {
                    case 403:
                        navigate('/');
                        break;
                    default:
                        break;
                }
            });
    };
};

