export const saveDeviceRequest = createAction('SAVE_DEVICE_REQUEST');
export const saveDeviceSuccess = createAction('SAVE_DEVICE_SUCCESS');
export const saveDeviceFailure = createAction('SAVE_DEVICE_FAILURE');

export const saveDevice = (device) => {
    return (dispatch, getState) => {
        const {
            auth: {
                cookie: { access_token },
            },
            data: {
                userDevices: { devices },
            },
        } = getState();

        const newDevices = _.uniqBy(
            [...devices, { name: device.name, uid: device.id }],
            'uid'
        );

        if (newDevices.length === devices.length) {
            return false;
        }

        dispatch(saveDeviceRequest());
        dispatch(showAppPreloader(true));

        axios
            .put(
                'user/devices/',
                { devices: newDevices },
                {
                    headers: {
                        Authorization: `Bearer ${access_token}`,
                    },
                }
            )
            .then((response) => {
                dispatch(saveDeviceSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch(() => {
                dispatch(saveDeviceFailure());
                dispatch(showAppPreloader(false));
            });
    };
};

export const removeDeviceRequest = createAction('REMOVE_DEVICE_REQUEST');
export const removeDeviceSuccess = createAction('REMOVE_DEVICE_SUCCESS');
export const removeDeviceFailure = createAction('REMOVE_DEVICE_FAILURE');

export const removeDevice = (uid) => {
    return (dispatch, getState) => {
        const {
            auth: {
                cookie: { access_token },
            },
            data: {
                userDevices: { devices },
            },
        } = getState();

        const newDevices = devices.filter((device) => {
            if (device.uid === uid) {
                return false;
            }
            return true;
        });

        dispatch(saveDeviceRequest());
        dispatch(showAppPreloader(true));

        axios
            .put(
                'user/devices/',
                { devices: newDevices },
                {
                    headers: {
                        Authorization: `Bearer ${access_token}`,
                    },
                }
            )
            .then((response) => {
                dispatch(removeDeviceSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch(() => {
                dispatch(removeDeviceFailure());
                dispatch(showAppPreloader(false));
            });
    };
};

