import Cookies from 'universal-cookie';

export const writeStateToCookie = (state, name, duration) => {
    const cookies = new Cookies();
    const expiresDate = new Date();
    // expiresDate.setDate(expiresDate.getDate() + duration);
    expiresDate.setSeconds(expiresDate.getSeconds() + duration);
    cookies.set(name, JSON.stringify(state), {
        path: '/',
        expires: expiresDate,
    });
};

export const readStateFromCookie = (name) => {
    const cookies = new Cookies();
    return cookies.get(name);
};

export const base64ToArrayBuffer = (base64) => {
    var binary_string = window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array(len);
    for (var i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
    }
    return bytes.buffer;
};
