import { handleActions } from 'redux-actions';
import * as actions from '../actions';
import * as bluetoothActions from '../actions/bluetooth';
import { base64ToArrayBuffer } from '../utils';

export default handleActions(
    {
        [actions.fetchRecipesSuccess](state, { payload }) {
            // console.log('fetchRecipesSuccess', payload);
            return {
                ...state,
                recipe: payload,
            };
        },
        [actions.fetchUserSuccess](state, { payload }) {
            // console.log('fetchUserSuccess', payload);
            return {
                ...state,
                user: payload,
            };
        },
        [actions.fetchUserDevicesSuccess](state, { payload }) {
            // console.log('fetchUserDevicesSuccess', payload);
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.getBinaryRecipeSuccess](state, { payload }) {
            // console.log('fetchUserDevicesSuccess', payload);
            let buffer = base64ToArrayBuffer(payload.binary_recipe);
            return {
                ...state,
                recipeBinary: { ...payload, buffer },
            };
        },
        [bluetoothActions.recipeByteTransfered](state, { payload }) {
            return {
                ...state,
                byteTransfered: state.byteTransfered + payload,
            };
        },
        [actions.saveDeviceSuccess](state, { payload }) {
            // console.log('saveDeviceSuccess', payload);
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.removeDeviceSuccess](state, { payload }) {
            // console.log('removeDeviceSuccess', payload);
            return {
                ...state,
                userDevices: payload,
            };
        },
    },
    {
        recipe: [],
        user: null,
        userDevices: null,
        recipeBinary: null,
        byteTransfered: 0,
    }
);
