    state = {
        bluetoothDevice: null,
        batteryLevelCharacteristic: null,
        gattServer: null,
        transferService: null,
        characteristic: null,
    };

    connectToDevice = () => {
        navigator.bluetooth
            .requestDevice({
                // acceptAllDevices: true,
                filters: [
                    { name: 'BlueNRG' },
                    // { name: 'JUST-ASUS' },
                    { services: ['5cf052d0-2403-4719-a558-b7e72c48112f'] },
                    // { services: ['00001800-0000-1000-8000-00805f9b34fb'] },
                ],
                optionalServices: ['5cf052d0-2403-4719-a558-b7e72c48112f'],
            })
            .then((device) => {
                console.log('device', device);
                this.setState({ bluetoothDevice: device });
                this.state.bluetoothDevice.addEventListener(
                    'gattserverdisconnected',
                    this.onDisconnected
                );
            })
            .catch((error) => {
                // alert(error);
                console.log('error', error);
            });
    };

    // onDisconnected() {
    //     // console.log('> Bluetooth Device disconnected');
