    connect() {
        // console.log('Connecting to Bluetooth Device...');
        return this.state.bluetoothDevice.gatt
            .connect()
            .then((server) => {
                console.log('server', server);

                this.setState({ gattServer: server });
            })
            .catch((error) => {
                alert(error);
                // console.log('error', error);
            });
    }

    serviceConnect = (uuid) => {
        const { server } = this.props;
        console.log('serviceConnect', server);
        server
            .getPrimaryService(uuid)
            .then((service) => {
                console.log('service', service);
                this.setState({ transferService: service });

                // return service.getCharacteristic(StatusCharacteristicUuid);
            })
            .catch((error) => {
                alert(error);
                // console.log('error', error);
            });
    };

    CtrlRxCharacteristicUuid = () => {
        let buffer = new ArrayBuffer(20);
        let msgKeyWord8 = 0x0a;
        let operationCode = 0xff;
        let dataSize32 = 800;
        let dataType16 = 0x0006;

        new DataView(buffer).setInt8(0, msgKeyWord8);
        new DataView(buffer).setInt8(1, operationCode);
        new DataView(buffer).setInt32(2, dataSize32);
        new DataView(buffer).setInt16(6, dataType16);

        console.log('BUFFER', buffer, new DataView(buffer));
        this.state.transferService
            .getCharacteristic('5cf052d3-2403-4719-a558-b7e72c48112f')
            .then((characteristic) => {
                console.log('characteristic Control Receive');
                return characteristic.writeValue(buffer);
            })
            .then((value) => {
                console.log('Control Receive value: ', value);
            })
            .catch((error) => {
                alert(error);
            });
    };

    sendDataToDevice = () => {
        let buffer = new ArrayBuffer(20);
        new DataView(buffer).setInt32(0, 6000);
        this.state.transferService
            .getCharacteristic('5cf052d1-2403-4719-a558-b7e72c48112f')
            .then((characteristic) => {
                console.log('characteristic Data Receive', buffer);

                let result = null;
                async function sendingData() {
                    for (let i = 0; i < 800; i = i + 20) {
                        result = await characteristic.writeValue(buffer);
                        console.log('sendingData', result);
                    }
                }
                sendingData();
            })
            .catch((error) => {
                alert(error);
            });
    };

    CtrlTxCharacteristicUuid = () => {
        this.state.transferService
            .getCharacteristic('5cf052d4-2403-4719-a558-b7e72c48112f')
            .then((characteristic) => {
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    this.handleResponseControlMsg
                );
                console.log('Подписка на Control Transmit');
            })
            .catch((error) => {
                alert(error);
            });
    };

    StatusCharacteristicUuid = () => {
        this.state.transferService
            .getCharacteristic('5cf052d5-2403-4719-a558-b7e72c48112f')
            .then((characteristic) => {
                console.log('characteristic Статус', characteristic);
                characteristic.startNotifications();
                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    this.handleLevelChanged
                );
                return characteristic.readValue();
            })
            .then((value) => {
                console.log('Статус value: ', value);
            });
    };

    batteryLevel = () => {
        this.state.transferService
            .getCharacteristic('battery_level')
            .then((characteristic) => {
                console.log('characteristic Статус', characteristic);
                characteristic.startNotifications();
                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    this.handleLevelChanged
                );
                return characteristic.readValue();
            })
            .then((value) => {
                console.log('Заряд батареи', value.buffer);
                // console.log('Battery percentage is ' + value.getUint8(0));
            });
    };

    handleResponseControlMsg(event) {
        let status = event.target.value.getUint8(0);
        console.log('handleResponseControlMsg', 0x0a === status);
        // console.log('Event - Battery percentage is ' + batteryLevel);
    }

    handleLevelChanged(event) {
        let batteryLevel = event.target.value;
        console.log('handleStatusChanged', batteryLevel, batteryLevel.buffer);
        // console.log('Event - Battery percentage is ' + batteryLevel);
    }

