import React, { Component } from 'react';
import { Card, Col, Row, Button, Tooltip, Divider, Progress } from 'antd';
import {
    PlusCircleOutlined,
    CloseCircleOutlined,
    CheckCircleTwoTone,
} from '@ant-design/icons';
import { connect } from 'react-redux';
import * as actions from '../../../actions';
import { connectToDevice } from '../../../actions/bluetooth';
import WebBluetooth from '../../WebBluetooth';

const mapStateToProps = (state) => {
    const {
        auth: { token },
        data,
        data: { byteTransfered },
        bluetooth: { device, server, batteryService },
    } = state;
    return {
        token,
        data,
        device,
        server,
        batteryService,
        byteTransfered,
    };
};

const actionCreators = {
    // convertToken: actions.convertToken,
    recipe: actions.recipe,
    user: actions.user,
    connectToDevice,
    removeDevice: actions.removeDevice,
};

class Devices extends Component {
    render() {
        const {
            connectToDevice,
            removeDevice,
            device: deviceFromStore,
            showDrawer,
            server,
            batteryService,
            byteTransfered,
            // batteryService: { level },
        } = this.props;
        // console.log('Devices', this.props.devices);
        let devices =
            this.props.devices &&
            this.props.devices.map((device) => {
                if (deviceFromStore && deviceFromStore.id === device.uid) {
                    return (
                        <Col span={8} key={device.uid}>
                            <Card
                                title={
                                    <>
                                        {deviceFromStore &&
                                            deviceFromStore.id ===
                                                device.uid && (
                                                <CheckCircleTwoTone twoToneColor="#52c41a" />
                                            )}
                                        <Divider type="vertical" />
                                        <span>{device.name}</span>
                                    </>
                                }
                                extra={
                                    <div>
                                        <Button
                                            // type="primary"
                                            style={{ color: 'red' }}
                                            shape="circle"
                                            icon={<CloseCircleOutlined />}
                                            onClick={() =>
                                                removeDevice(device.uid)
                                            }
                                        />
                                    </div>
                                }
                                style={{ width: 300 }}
                                // cover={<img alt="example" src={device.image} />}
                            >
                                <p>
                                    <strong>UID:</strong> {device.uid}
                                </p>
                                {batteryService &&
                                    Number.isInteger(batteryService.level) && (
                                        <p>
                                            <strong>Battery level: </strong>
                                            {batteryService.level}
                                        </p>
                                    )}

                                {server && (
                                    <Button onClick={showDrawer}>
                                        Add recipe to device
                                    </Button>
                                )}
                                {byteTransfered ? (
                                    <Progress
                                        type="circle"
                                        percent={Math.floor(
                                            (byteTransfered * 100) / 9012
                                        )}
                                        width={40}
                                    />
                                ) : (
                                    ''
                                )}
                            </Card>
                        </Col>
                    );
                }
                return (
                    <Col span={8} key={device.uid}>
                        <Card
                            title={
                                <>
                                    {deviceFromStore &&
                                        deviceFromStore.id === device.uid && (
                                            <CheckCircleTwoTone twoToneColor="#52c41a" />
                                        )}
                                    <Divider type="vertical" />
                                    <span>{device.name}</span>
                                </>
                            }
                            extra={
                                <div>
                                    <Button
                                        // type="primary"
                                        style={{ color: 'red' }}
                                        shape="circle"
                                        icon={
                                            <CloseCircleOutlined twoToneColor="#52c41a" />
                                        }
                                        onClick={() => removeDevice(device.uid)}
                                    />
                                </div>
                            }
                            style={{ width: 300 }}
                            // cover={<img alt="example" src={device.image} />}
                        >
                            <p>
                                <strong>UID:</strong> {device.uid}
                            </p>
                            <Button
                                // type="primary"
                                shape="circle"
                                icon={<PlusCircleOutlined />}
                                onClick={() =>
                                    connectToDevice([{ name: device.name }])
                                }
                            />
                        </Card>
                    </Col>
                );
            });
        // console.log('fsadasd', devices);
        return (
            <>
                <Row gutter={16}>{devices || ''}</Row>
                <Divider />
                <Tooltip title="Add device">
                    <Button
                        shape="circle"
                        icon={<PlusCircleOutlined />}
                        onClick={() => connectToDevice()}
                    />
                </Tooltip>
                <WebBluetooth />
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(Devices);
