export const connectToDeviceRequest = createAction('CONNECT_TO_DEVICE_REQUEST');
export const connectToDeviceSuccess = createAction('CONNECT_TO_DEVICE_SUCCESS');
export const connectToDeviceFailure = createAction('CONNECT_TO_DEVICE_FAILURE');

// Подключение к выбраному устройству
export const connectToDevice = (newFilters) => {
    return (dispatch, getState) => {
        const {
            bluetooth: { devices: devicesFromStore },
        } = getState();

        dispatch(connectToDeviceRequest());

        const filters = newFilters || [{ namePrefix: 'Blue' }];
        navigator.bluetooth
            .requestDevice({
                acceptAllDevices: filters ? undefined : true,
                filters,
                optionalServices: [
                    '5cf052d0-2403-4719-a558-b7e72c48112f',
                    'battery_service',
                    0x2a29,
                    0x180a,
                ],
            })
            .then((device) => {
                if (
                    _.find(
                        devicesFromStore,
                        (deviceFromStore) => deviceFromStore.id === device.id
                    )
                ) {
                    throw new Error('device has already been added');
                } else {
                    console.info(
                        '%cconnected device:',
                        'color:green; font-size:2em; border-top:10px solid red'
                    );
                    console.table(device);

                    dispatch(connectToDeviceSuccess(device));
                    dispatch(saveDevice(device));
                    dispatch(connectToGATTServer(device));
                }
            })
            .catch((error = { message: 'connectToDeviceFailure' }) => {
                console.error(error.message);
                dispatch(connectToDeviceFailure());
            });
    };
};

export const connectGattRequest = createAction('CONNECT_GATT_REQUEST');
export const connectGattSuccess = createAction('CONNECT_GATT_SUCCESS');
export const connectGattFailure = createAction('CONNECT_GATT_FAILURE');

// Подключение к GATT серверу выбранного устройства
export const connectToGATTServer = (device) => {
    return (dispatch) => {
        dispatch(connectGattRequest());
        device.gatt
            .connect()
            .then((server) => {
                console.info(
                    '%cGATT server:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.table(server);

                dispatch(connectGattSuccess(server));
            })
            .catch(() => {
                console.error('connectGattFailure');
                dispatch(connectGattFailure());
            });
    };
};

export const fetchBatteryServiceRequest = createAction(
    'FETCH_BATTERY_SERVICE_REQUEST'
);
export const fetchBatteryServiceSuccess = createAction(
    'FETCH_BATTERY_SERVICE_SUCCESS'
);
export const fetchBatteryServiceFailure = createAction(
    'FETCH_BATTERY_SERVICE_FAILURE'
);

export const fetchBatteryCharacteristicSuccess = createAction(
    'FETCH_BATTERY_CHARACTERISTIC_SUCCESS'
);

export const batteryService = () => {
    return (dispatch, getState) => {
        dispatch(fetchBatteryServiceRequest());

        const {
            bluetooth: { server },
        } = getState();

        server
            .getPrimaryService('battery_service')
            .then((service) => {
                dispatch(fetchBatteryServiceSuccess());
                return service.getCharacteristic('battery_level');
            })
            .catch(() => {
                dispatch(fetchBatteryServiceFailure());
            })
            .then((characteristic) => {
                if (characteristic) {
                    characteristic.startNotifications();
                    characteristic.addEventListener(
                        'characteristicvaluechanged',
                        handleBatteryLevelChange
                    );
                    return characteristic.readValue();
                }
                throw new Error('characteristic baterry level not found');
            })
            .then((value) => {
                dispatch(
                    fetchBatteryServiceSuccess({ level: value.getUint8(0) })
                );
            })
            .catch(() => {});

        function handleBatteryLevelChange(event) {
            dispatch(
                fetchBatteryServiceSuccess({
                    level: event.target.value.getUint8(0),
                })
            );
        }
    };
};

export const fetchDeviceInfoRequest = createAction('FETCH_DEVICE_INFO_REQUEST');
export const fetchDeviceInfoSuccess = createAction('FETCH_DEVICE_INFO_SUCCESS');
export const fetchDeviceInfoFailure = createAction('FETCH_DEVICE_INFO_FAILURE');

export const fetchDeviceInfoCharacteristicSuccess = createAction(
    'FETCH_DEVICE_INFO_CHARACTERISTIC_SUCCESS'
);

export const deviceInfoService = () => {
    return (dispatch, getState) => {
        dispatch(fetchDeviceInfoRequest());

        const {
            bluetooth: { server },
        } = getState();

        server
            .getPrimaryService(0x180a)
            .then((service) => {
                dispatch(fetchDeviceInfoSuccess());
                return service.getCharacteristic(0x2a29);
            })
            .catch(() => {
                dispatch(fetchDeviceInfoFailure());
            })
            .then((characteristic) => {
                if (characteristic) {
                    return characteristic.readValue();
                }
                throw new Error('characteristic manufacturerName not found');
            })
            .then((value) => {
                dispatch(
                    fetchDeviceInfoCharacteristicSuccess({
                        manufacturerName: value.getUint8(0),
                    })
                );
            })
            .catch(() => {});
    };
};

export const writeRecipeToDeviceRequest = createAction(
    'WRITE_RECIPE_TO_DEVICE_REQUEST'
);
export const writeRecipeToDeviceSuccess = createAction(
    'WRITE_RECIPE_TO_DEVICE_SUCCESS'
);
export const writeRecipeToDeviceFailure = createAction(
    'WRITE_RECIPE_TO_DEVICE_FAILURE'
);

export const recipeByteTransfered = createAction('RECIPE_BYTE_TRANSFERED');

let keyI = 0;
let characteristicWrite = null;

export const writeRecipeToDevice = () => {
    return (dispatch, getState) => {
        const {
            bluetooth: { server },
            data: { recipeBinary },
        } = getState();

        let service = null;

        // connect to service

        server
            .getPrimaryService(dataTransferService.uuid)
            .then((ser) => {
                service = ser;
                return service.getCharacteristic(
                    '5cf052d4-2403-4719-a558-b7e72c48112f'
                );
            })
            .then((characteristic) => {
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    handleResponseControlMsg
                );
                console.log('Подписка на Control Transmit');

                return service.getCharacteristic(
                    '5cf052d3-2403-4719-a558-b7e72c48112f'
                );
            })
            .then((characteristic) => {
                console.log('characteristic Control Receive');
                let buffer = new ArrayBuffer(20);
                let msgKeyWord8 = 0x0a;
                let operationCode = 0xff;
                let dataSize32 = 0x2334;
                let dataType16 = 0x0;

                new DataView(buffer).setInt8(0, msgKeyWord8, true);
                new DataView(buffer).setInt8(1, operationCode, true);
                new DataView(buffer).setInt32(2, dataSize32, true);
                new DataView(buffer).setInt16(6, dataType16, true);
                console.log('Control Receive', new DataView(buffer).getInt8(0));
                characteristic.writeValue(new Int8Array(buffer));
            })
            .catch((error) => {
                alert(error);
            });

        function handleResponseControlMsg(event) {
            let status = event.target.value.getUint8(0);
            console.log('handleResponseControlMsg', 0x0a === status, status);
            if (0x0a === status) {
                service
                    .getCharacteristic('5cf052d5-2403-4719-a558-b7e72c48112f')
                    .then((characteristic) => {
                        characteristic.startNotifications();
                        characteristic.addEventListener(
                            'characteristicvaluechanged',
                            handleLevelChanged
                        );
                        return service.getCharacteristic(
                            '5cf052d1-2403-4719-a558-b7e72c48112f'
                        );
                    })
                    .then((characteristic) => {
                        characteristicWrite = characteristic;

                        sendingData();
                    });
            } else if (0x0f === status) {
                console.log('Recipe did saved');
            } else {
                console.log('');
            }
        }

        function handleLevelChanged(event) {
            let value = event.target.value;
            console.log('handleStatusChanged', value, value.getUint8(0));
            sendingData();
        }

        async function sendingData() {
            console.log(
                'sendingData...'
                // recipeBinary.buffer,
                // characteristicWrite
            );

            for (
                let sum = 0;
                keyI < recipeBinary.buffer.byteLength && sum < 400;
                keyI = keyI + 20, sum = sum + 20
            ) {
                await characteristicWrite.writeValue(
                    recipeBinary.buffer.slice(keyI, keyI + 20)
                );
                dispatch(
                    recipeByteTransfered(
                        recipeBinary.buffer.slice(keyI, keyI + 20).byteLength
                    )
                );
                console.log(
                    'DataBuffer',
                    recipeBinary.buffer.slice(keyI, keyI + 20)
                );
            }

            // for (let i = 0; keyI < buffer.byteLength; i++) {
            //     result = await characteristicWrite.writeValue(
            //         buffer.slice(keyI, keyI + 20)
            //     );
            //     keyI = keyI + 20;
            //     console.log('sendingData', result);
            //     break;
            // }
        }
    };
};
