import React, { Component } from 'react';
import { Router, Link, Redirect, navigate } from '@reach/router';
import { connect } from 'react-redux';
import { Result, Button } from 'antd';

import * as actions from './actions';
import { readStateFromCookie } from './utils';
import 'antd/dist/antd.css';
import './App.css';
import Loader from './components/Loader';
// import WebBluetooth from './WebBluetooth';
// import GoogleOauth from './components/GoogleOauth';
import Login from './components/routes/Login';
import Account from './components/routes/Account';
import BluetoothNotAvailable from './components/routes/BluetoothNotAvailable';

const mapStateToProps = (state) => {
    const {
        auth: { cookie },
        settings: { showPreloader },
    } = state;
    return {
        cookie,
        showPreloader,
    };
};

const actionCreators = {
    fetchTokenSuccess: actions.fetchTokenSuccess,
};

class App extends Component {
    componentWillMount() {
        const { fetchTokenSuccess } = this.props;
        const authCookie = readStateFromCookie('auth_cookie');
        // Проверяем наличии куки авторизации
        if (authCookie) {
            console.info(
                '%ccookie:',
                'color:green; font-size:2em; border-top:10px solid red'
            );
            console.table(authCookie);
            fetchTokenSuccess(authCookie);
        }
    }

    render() {
        // navigator.bluetooth.getAvailability().then((available) => {
        //     if (available) console.log('This device supports Bluetooth!');
        //     else console.log('Doh! Bluetooth is not supported');
        // });
        // navigator.bluetooth.onavailabilitychanged = function (event) {
        //     console.log('availabilitychanged', event);
        // };
        // const bluetoothIsAvailable = !!navigator.bluetooth;

        if (!navigator.bluetooth) {
            navigate('/bluetooth-not-available');
        }

        const { cookie, showPreloader } = this.props;
        return (
            <>
                {showPreloader && <Loader />}
                <Router basepath="elements2">
                    {/* {!bluetoothIsAvailable ? (
                        <Redirect
                            // from="*"
                            to="bluetooth-not-available"
                            noThrow
                        />
                    ) : null} */}

                    {cookie.access_token && (
                        <Redirect from="/" to="account" noThrow />
                    )}

                    <Login path="/" />
                    <Account path="account" />
                    <BluetoothNotAvailable path="bluetooth-not-available" />

                    <Result
                        default
                        status="404"
                        title="404"
                        subTitle="Sorry, the page you visited does not exist."
                        extra={
                            <Button type="primary">
                                <Link to="/">Back Home</Link>
                            </Button>
                        }
                    />
                </Router>
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(App);
