import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Menu, PageHeader, Drawer } from 'antd';
import { AntDesignOutlined, ReadOutlined } from '@ant-design/icons';

import './account.scss';

import * as actions from '../../../actions';
import Devices from './Devices';
import Recipes from './Recipes';

const mapStateToProps = (state) => {
    const {
        auth: { token },
        data,
    } = state;
    return {
        token,
        data,
    };
};

const actionCreators = {
    // convertToken: actions.convertToken,
    recipe: actions.recipe,
    user: actions.user,
    userDevices: actions.userDevices,
};

class Account extends Component {
    state = {
        current: 'devices',
        showDrawer: false,
    };

    showDrawer = () => {
        this.setState({ showDrawer: true });
    };

    onClose = () => {
        this.setState({ showDrawer: false });
    };

    handleClick = (e) => {
        // console.log('click ', e);
        this.setState({ current: e.key });
    };

    componentDidMount() {
        const { recipe, user, userDevices } = this.props;
        // console.log('didMount');
        recipe();
        user();
        userDevices();
    }

    render() {
        const { current } = this.state;
        const {
            data: { user, recipe, userDevices },
        } = this.props;
        // console.log('account', user);
        return (
            <>
                <div className="elements__account">
                    <PageHeader
                        title={
                            <div>
                                {user && user.first_name + ' ' + user.last_name}
                            </div>
                        }
                    >
                        <Menu
                            onClick={this.handleClick}
                            selectedKeys={[current]}
                            mode="horizontal"
                        >
                            <Menu.Item
                                key="devices"
                                icon={<AntDesignOutlined />}
                            >
                                My devices
                            </Menu.Item>
                            <Menu.Item key="recipes" icon={<ReadOutlined />}>
                                Recipes
                            </Menu.Item>
                        </Menu>
                        {/* <WebBluetooth /> */}
                    </PageHeader>
                    {this.state.current === 'devices' ? (
                        <Devices
                            devices={userDevices && userDevices.devices}
                            showDrawer={this.showDrawer}
                        />
                    ) : (
                        <Recipes
                            recipes={recipe}
                            buttonVisible={true}
                            onClose={this.onClose}
                        />
                    )}
                    <Drawer
                        title="Recipes"
                        width={640}
                        placement="right"
                        closable={false}
                        onClose={this.onClose}
                        visible={this.state.showDrawer}
                    >
                        <Recipes
                            recipes={recipe}
                            buttonVisible={true}
                            onClose={this.onClose}
                        />
                    </Drawer>
                </div>
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(Account);
