import { handleActions } from 'redux-actions';
import * as actions from '../actions';

export default handleActions(
    {
        [actions.showAppPreloader](state, { payload }) {
            // Stack for handle several requests
            let stack = state.preloaderStack.slice();
            if (payload) {
                stack.push(0);
            } else {
                stack.shift();
            }
            return {
                ...state,
                showPreloader: !!stack.length,
                preloaderStack: stack,
            };
        },
    },
    {
        showPreloader: false,
        preloaderStack: [],
    }
);
