import React, { Component } from 'react';
import { connect } from 'react-redux';

import FacebookLogin from 'react-facebook-login';
import * as actions from '../../actions';

const mapStateToProps = (state) => {
    const {
        auth: { token },
    } = state;
    return {
        token,
    };
};

const actionCreators = {
    convertToken: actions.convertToken,
};

class FacebookOauth extends Component {
    responseFacebook = (response) => {
        const { convertToken } = this.props;
        convertToken(
            response.accessToken,
            'facebook',
            'qxN1hlSy0GbXqE6au6Hd63N4YOWK6WCP2TuXWzaO'
        );
    };

    render() {
        return (
            <>
                <FacebookLogin
                    appId="369580627566109"
                    autoLoad={false}
                    fields="name,email,picture"
                    // onClick={componentClicked}
                    callback={this.responseFacebook}
                />
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(FacebookOauth);
