import React, { Component } from 'react';
import { connect } from 'react-redux';

import GoogleLogin from 'react-google-login';
import * as actions from '../../actions';

// const responseGoogle = (response, status) => {
//     console.log(response, status);
// };

const mapStateToProps = (state) => {
    const {
        auth: { token },
    } = state;
    return {
        token,
    };
};

const actionCreators = {
    convertToken: actions.convertToken,
};

class GoogleOauth extends Component {
    responseGoogleRequest = () => {
        // console.log(response, 'request');
    };

    responseGoogleSuccess = (response) => {
        const { convertToken } = this.props;
        convertToken(
            response.accessToken,
            'google-oauth2',
            'qxN1hlSy0GbXqE6au6Hd63N4YOWK6WCP2TuXWzaO'
        );
    };

    responseGoogleFailure = () => {
        // console.log(response, 'failure');
    };

    render() {
        return (
            <GoogleLogin
                // clientId="385449723022-vmed4lc1a8a1t3fsat4flujbobsblv01.apps.googleusercontent.com"
                clientId="205221816405-0lvlolma2t1lvif67t749qee7h9te473.apps.googleusercontent.com"
                // redirectUri="http://localhost:3000"
                buttonText="Login"
                onSuccess={this.responseGoogleSuccess}
                onFailure={this.responseGoogleFailure}
                onRequest={this.responseGoogleRequest}
                cookiePolicy={'single_host_origin'}
            />
        );
    }
}

export default connect(mapStateToProps, actionCreators)(GoogleOauth);
