import React, { Component } from 'react';
import { Card, Row, Button } from 'antd';
import { connect } from 'react-redux';
import * as actions from '../../../actions';
import * as bluetoothActions from '../../../actions/bluetooth';

const mapStateToProps = (state) => {
    const {
        auth: { token },
    } = state;
    return {
        token,
    };
};

const actionCreators = {
    writeRecipeToDevice: bluetoothActions.writeRecipeToDevice,
    getBinaryRecipe: actions.getBinaryRecipe,
};

class Recipes extends Component {
    render() {
        const { onClose, getBinaryRecipe } = this.props;
        let recipes = this.props.recipes.map((recipe) => {
            return (
                // <Col span={6} key={recipe.id}>
                <Card
                    key={recipe.id}
                    title={recipe.name + ' - id' + recipe.id + ''}
                    // extra={<a href="#">More</a>}
                    style={{ width: 250 }}
                    cover={<img alt="example" src={recipe.image} />}
                    className="recipe__card"
                >
                    <p>{recipe.description_text}</p>
                    {this.props.buttonVisible && (
                        <Button
                            onClick={() => {
                                onClose();
                                getBinaryRecipe(recipe.id);
                            }}
                        >
                            Add
                        </Button>
                    )}
                </Card>
                // </Col>
            );
        });
        // console.log('fsadasd', recipes);
        return <Row gutter={16}>{recipes}</Row>;
    }
}

export default connect(mapStateToProps, actionCreators)(Recipes);
